
.off-canvas.position-right{border-left:1px solid #ddd
}
.nav.navbar-stacked{overflow:hidden;padding:0;margin:0;list-style:none;position:fixed;background:#fff
}
.nav.navbar-stacked li{border-bottom:1px solid #eee;position:relative;padding:15px 10px 15px 35px;border-bottom:1px solid #eee;position:relative
}
.nav.navbar-stacked li a{padding:10px 0 !important;color:#555 !important;text-indent:-40px;margin-left:25px;display:block
}
.nav.navbar-stacked li a span{color:#fff;border-radius:50%;line-height:30px;text-align:center;margin:0 10px 0 0;padding:8px
}
.nav.navbar-stacked li a i{color:#ccc;font-size:10px !important;position:relative;left:-5px
}
.nav.navbar-stacked li a small{text-align:right;background:#fff;color:#ccc;border:1px solid #ddd;padding:3px 7px;border-radius:15px;margin:0 0 0 7px;font-size:10px
}
.nav.navbar-stacked li a img{width:95%;padding:0;border-radius:4px;margin:10px 0
}
.nav.navbar-stacked.sidebar-right-content{padding-bottom:150px
}



