#weather{z-index:10;min-width:100%;min-height:400px;margin:0;position:relative
}
#weather .panelLeft{width:100%;height:100%;min-height:400px;display:inline-block;box-sizing:border-box;margin:0;padding:0;position:relative
}
#weather .panelRight{width:45%;height:100%;display:inline-block;box-sizing:border-box;margin:0;padding:0;position:absolute;top:0;right:0
}
#weather .panelLeft{background:linear-gradient(to right, rgba(0,77,102,0.4), rgba(204,0,204,0.2));background-blend-mode:overlay
}
#weather .panelLeft img{width:100%;height:100%;position:absolute;top:0;left:0;border-radius:4px 0 0 4px;z-index:-1
}
