}
#weather .panelLeft .bottom .temp p:nth-child(2){font-weight:300;padding-left:8px
}
#weather .panelRight{float:right
}
#weather .panelRight:after,#weather .panelRight:before{clear:both
}
#weather .panelRight>*{padding:15px 30px
}
#weather .panelRight .customHeight{width:100%;background:rgba(50,170,250,0.5);color:rgba(255,255,255,0.7)
}
#weather .panelRight .customHeight ul{list-style:none;padding:0;margin:20px 0 0
}
