}
#weather .panelRight .remainingHeight{background:rgba(255,255,255,0.9)
}
#weather .panelRight .remainingHeight ul.inline{border-bottom:1px solid rgba(204,204,204,0.8);list-style:none;padding:0 0 15px
}
#weather .panelRight .remainingHeight ul.inline li{display:inline-block;padding-bottom:15px;margin-right:15px;position:relative;text-align:center;transition:.5s all
}
#weather .panelRight .remainingHeight ul.inline li:after{height:2px;background:transparent;display:block;content:"";width:100%;position:absolute;bottom:0px;transition:.5s all
}
#weather .panelRight .remainingHeight ul.inline li.active,#weather .panelRight .remainingHeight ul.inline li:hover{color:rgba(0,0,0,0.9)
}
#weather .panelRight .remainingHeight ul.inline li.active:after,#weather .panelRight .remainingHeight ul.inline li:hover:after{background:tomato
}
